import React from 'react'

import './ProductsList.css'
import ProductListItem from './ProductListItem';

import products from './products'

const ProductsList = () =>
    <div>
        <h1>ProductsList</h1>
        <div className = "row">
            {
                products.map(({
                    id,
                    name,
                    description,
                    type,
                    capacity,
                    price,
                    image,
                }) => (
                    <div className = "col-lg-6" key = {id}>
                        <ProductListItem
                            id = {id}
                            image = {image}
                            name = {name}
                            description = {description}
                            type = {type}
                            capacity = {capacity}
                            price = {price}
                        />
                    </div>
                ))
            }
        </div>
    </div>

export default ProductsList