import React from 'react'

import './filter.css'

const Filter = () => 
<div className="filter">
    <p className="filter--title">Device Type:</p>
        <input type="checkbox" id="laptopCheckbox" className="inputCheckbox" />
        <label htmlFor="laptopCheckbox" className="labelCheckbox">Laptop</label>
        <input type="checkbox" id="tabletCheckbox" className="inputCheckbox" />
        <label htmlFor="tabletCheckbox" className="labelCheckbox">Tablet</label>
    <p className="filter--title">Color:</p>
        <input type="checkbox" id="blackCheckbox" className="inputCheckbox" />
        <label htmlFor="blackCheckbox" className="labelCheckbox">Black</label>
        <input type="checkbox" id="redCheckbox" className="inputCheckbox" />
        <label htmlFor="redCheckbox" className="labelCheckbox">Red</label>
        <input type="checkbox" id="greenCheckbox" className="inputCheckbox" />
        <label htmlFor="greenCheckbox" className="labelCheckbox">Green</label>
        <input type="checkbox" id="blueCheckbox" className="inputCheckbox" />
        <label htmlFor="blueCheckbox" className="labelCheckbox">Blue</label>
        <input type="checkbox" id="orangeCheckbox" className="inputCheckbox" />
        <label htmlFor="orangeCheckbox" className="labelCheckbox">Orange</label>
        <input type="checkbox" id="whiteCheckbox" className="inputCheckbox" />
        <label htmlFor="whiteCheckbox" className="labelCheckbox">White</label>
    <p className="filter--title">Ram:</p>
        <input type="checkbox" id="oneGbCheckbox" className="inputCheckbox" />
        <label htmlFor="oneGbCheckbox" className="labelCheckbox">1 Gb</label>
        <input type="checkbox" id="twoGbCheckbox" className="inputCheckbox" />
        <label htmlFor="twoGbCheckbox" className="labelCheckbox">2 Gb</label>
        <input type="checkbox" id="fourGbCheckbox" className="inputCheckbox" />
        <label htmlFor="fourGbCheckbox" className="labelCheckbox">4 Gb</label>
        <input type="checkbox" id="sixGbCheckbox" className="inputCheckbox" />
        <label htmlFor="sixGbCheckbox" className="labelCheckbox">6 Gb</label>
        <input type="checkbox" id="eightGbCheckbox" className="inputCheckbox" />
        <label htmlFor="eightGbCheckbox" className="labelCheckbox">8 Gb</label>
    <button className="btn-submit">
        Submit
        <svg width="100%" height="50" xmlns="http://www.w3.org/2000/svg">
            <rect x="0" y="0" fill="none" width="100%" height="50" />
        </svg>
    </button>
</div>

export default Filter