import React from 'react'

const QuantityInput = ({
    productCount,
    onDecrementClick,
    onIncrementClick,
    minCount = Number.MIN_SAFE_INTEGER,
    maxCount = Number.MAX_SAFE_INTEGER,
}) => {
    return (
        <div className = "product-quantity">
            <button onClick = {() => onDecrementClick()} disabled={productCount <= minCount}>-</button>
            <input type = "text" value = {productCount} readOnly/>
            <button onClick = {() => onIncrementClick()} disabled={productCount >= maxCount}>+</button>
        </div>
    )
}

export default QuantityInput